.contact-page{
    padding-top: 70px;
    overflow: hidden;
    .nav{
        background-color: #fff;
        border-bottom: 1px solid #ccc;

        .nav-logo{
            .logo-container{
                content: url('../../../../assets/Navbar/blue-logo.png') !important;
            }
            h6{
                color:#111 !important;
            }
        }
        .nav-links{
            .inner-nav-link{
                color:#000 !important;
                svg{
                    color:#444;
                }
            }
        }
        .nav-auth{
            .nav-login a{
                color:#000 !important;
            }
        }
    }
    .nav__sticky{
        border:0;
    }
    .mobile-toolbar{
        background-color: white;
        border-bottom: 1px solid #ccc;
        transition: 0.2s ease;
        .mobile-logo-row{
            h6{
                color:#333 !important;
            }
        }
        button{
            color:#222 !important;
        }
    }
    .toolbar__sticky{
        border: 0;
    }
}

.contact-page .banner{
    clip-path: polygon(0 0%, 100% 0, 100% 65%, 50% 100%, 0 65%);
    background-image: url('../../../../assets/Contact/banner.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding:7.5% 7.5% 10%;
    text-align: center;
    color:white;
    h1{
        font-size: 45px;
        margin-bottom: 30px;
    }
    p{
        font-size: 18px;
    }
}

.contact-page .items-row{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin:5% 0;
    padding:0 10%;
    .item{
        width: 31%;
        position: relative;
        border: 1px solid #ddd;
        padding:30px;
        border-radius: 10px;
        &:last-child{
            .link2{
                display: none;
            }
        }
    }
    .icon-container{
        background-color: #e2ebff;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100px;
        height: 100px;
        border-radius: 50%;
    }
    .icon{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 75px;
        height: 75px;
        background-color: #fff;
        border-radius: 72% 28% 36% 64% / 65% 72% 28% 35%;
        font-size: 45px;
        color:#427CFF;
    }
    h6{
        font-size: 18px;
        margin:30px 0 15px
    }
    p{
        font-size: 18px;
        color:#555;
        margin-bottom: 60px;
    }
    .links{
        position: absolute;
        bottom:25px;
        display: flex;
    }
    a{
        border:1px solid #427CFF;
        color:#427CFF;
        font-size: 17px;
        padding:8px 15px;
        border-radius: 20px;
        transition: 0.15s ease-in-out;
        -webkit-transition: 0.15s ease-in-out;
        &:hover{
            background: #427CFF;
            color:#fff;
        }
    }
    .link1{
        margin-right: 20px;
    }
}

@media(max-width:1401px){
    .contact-page .banner{
        h1{
            font-size: 35px;
            margin-bottom: 20px;
        }
        p{
            font-size: 15px;
        }
    }
    
    .contact-page .items-row{
        .item{
            padding:20px;
        }
        .icon-container{
            width: 80px;
            height: 80px;
        }
        .icon{
            width: 55px;
            height: 55px;
            font-size: 35px;
        }
        h6{
            font-size: 15px;
            margin:25px 0 10px;
        }
        p{
            font-size: 15px;
            margin-bottom: 50px;
        }
        .links{
            bottom:15px;
        }
        a{ 
            font-size: 14px;
            padding:7px 13px;
            border-radius: 20px;
        }
        .link1{
            margin-right: 15px;
        }
    }
}

@media(max-width:1199px){
    .contact-page .items-row{
        padding:0 5%;
        .item{
            width: 32%;
            padding:15px;
        }
    }
}

@media(max-width:930px){
    .contact-page .items-row{
        .item{
            width: 47.5%;
            padding:20px;
            margin:15px 0;
        }
    }
}

@media(max-width:767px){
    
    .contact-page .banner{
        clip-path: polygon(0 1%, 100% 0, 100% 85%, 50% 100%, 0 85%);
        padding:90px 7.5%;
        h1{
            font-size: 20px;
            margin-bottom: 15px;
        }
        p{
            font-size: 14px;
        }
    }
}

@media(max-width:650px){
    .contact-page .items-row{
        margin:25px 0;
        padding:0 7.5%;
        .item{
            width: 350px;
            max-width: 100%;
            margin:15px auto;
        }
        .icon-container{
            width: 70px;
            height: 70px;
        }
        .icon{
            width: 45px;
            height: 45px;
            font-size: 25px;
        }
        p{
            font-size: 14px;
            margin-bottom: 15px;
        }
        .links{
            flex-wrap: wrap;
            position: relative;
            bottom: unset;
        }
        a{ 
            font-size: 14px;
            padding:8px 17.5px;
            border-radius: 20px;
            margin:5px 0;
        }
        .link1{
            margin-right: 15px;
        }
    }
}

.contact-page .items-row{
    .item{
        display: inline-block;
        vertical-align: middle;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        overflow: hidden;
    }
     .item:before {
        content: "";
        position: absolute;
        z-index: -1;
        left: 51%;
        right: 51%;
        bottom: 0;
        background: #427CFF;
        height: 4px;
        -webkit-transition-property: left, right;
        transition-property: left, right;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }
    .item:hover:before{
        left: 0;
        right: 0;
    }
    .item:hover{
        transform:translateY(-20px);
        transition: 0.4s ease-in-out;
    }
}